@import './Checkout.scss';
.App {
    text-align: center;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    margin: 0 auto;
    align-items: center;
}
.nav {
    margin-top: 25px;
}
.nav-item {
    text-decoration: none;
    margin-left: 5px;
}
.App-link {
    color: #61dafb;
}
.welcome {
    display: flex;
    flex-direction: column;
    margin-top: 15vh;
    text-align: center;
    align-items: center;
}
.uppy-Dashboard-inner {
    text-align: center !important;
}
.dashboard button {
    margin: 30px;
}
.container {
    display: flex;
}
.registration,
.img-pane {
    display: flex;
    flex-direction: column;
    margin: 0 auto;
    margin-top: 25vh;
    align-items: center;
}
.profile-edit {
    padding: 20px;
}
.doc-preview {
    width: 150px;
    flex-basis: 20%;
    padding: 5px;
    height: 150px;
    object-fit: cover;
    margin: 5px;
}
.doc-view {
    width: 400px;
}
.docs {
    width: 800px;
    flex-flow: row wrap;
    justify-content: space-between;
}
.StripeElement,
.checkout-form input {
    min-width: 300px;
    padding: 10px;
    border: 2px solid #eee;
    border-radius: 5px;
}
.checkout-form {
    align-self: center;
    margin: 30px;
}
.card-wrap img {
    display: flex;
}
.checkout-form .form-group {
    display: flex;
    flex-direction: column;
}
.success-links {
    display: flex;
    flex-direction: column;
}
.form-button {
    margin: 0;
}
label svg {
    vertical-align: baseline;
}
.zephyr-input {
    border: 0px;
    border-bottom: 2px solid #999;
    border-radius: 0px;
    margin-bottom: 1em;
}
.zephyr-input:focus {
    box-shadow: none;
    border: 0;
    border-bottom: 3px solid #444;
}
.uppy-Dashboard-inner {
    width: 750px;
    height: 550px;
    margin: 0 auto;
    margin-bottom: 50px;
}
.paycard {
    padding: 10px;
    border-bottom: 10px solid lightblue;
    border-bottom-left-radius: 0.625em;
    border-bottom-right-radius: 0.625em;
    display: flex;
    flex-direction: row;
    transition: border-bottom 0.5s;
}
.paycard:hover {
    border-bottom: 10px solid rgb(47, 186, 233);
    border-radius: 0.625em;
    transition: border-bottom 0.5s;
}
.paycard:focus {
    border-color: #2fbae9;
    box-shadow: 0 0 0 0.2rem rgba(47, 186, 233, 0.5);
    outline: none;
}
.payselected {
    border-bottom: 10px solid rgb(47, 186, 233);
    border-radius: 0.625em;
    transition: border-bottom 0.5s;
}

.brand {
    align-items: flex-start;
    max-width: 10%;
    margin: 0 25px;
}
.form-button .brand {
    max-width: 10%;
    margin: 5px;
    margin-left: 15px;
}
.stripe-badge {
    margin: 0 auto;
}
